<template>
  <div class="one mescroll">
    <!--div class="top">
     <img :src="pics.length?$utils.dealImg(pics[0]):''" alt /> 
     <div class="top_mid">
        <img src="@/assets/img/card/one_top1.png" alt="">
      </div>-->
      <!-- <div class="top_bottom">
        <div class="left">
          <van-icon name="bullhorn-o" size="20" />
        </div>
        <div class="flex-mid">
          <van-swipe vertical class="notice-swipe" :autoplay="3000" :show-indicators="false">
            <van-swipe-item v-for="(item,index) in list" :key="index">{{item.userName}}</van-swipe-item>
          </van-swipe>
        </div>
        <div class="right">已报名</div>
      </div> 
    </div>-->
    <div class="bottom">
      <div v-html="msg" class="content">{{ msg }}</div>
    </div>
    <div class="fixed" @click="toMid">
      <div class="flex">
        <div class="left">免费领取</div>
        <div class="right">包邮到家</div>
      </div>
    </div>
  </div>
</template>
<script>
import { Icon, Swipe, SwipeItem } from "vant";
import areaList from "@/utils/AreaList";
import { cardSubmit, getNewOrder, getGoodsDetail, getPage, wxApi } from "@/api";
export default {
  components: {
    [Icon.name]: Icon,
    [Swipe.name]: Swipe,
    [SwipeItem.name]: SwipeItem,
  },
  data() {
    return {
      list: [],
      msg: "",
      pics: [],
      goodsDetail: {},
    };
  },
  methods: {
    toMid() {
      this.$router.push({
        name: "cardThreeInfo",
        query: this.$route.query,
      });
    },
    loadData() {
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true, // 禁用背景点击
      });
      // getNewOrder().then((res) => {
      //   loading.clear();
      //   if (res.code == 0) {
      //     this.list = res.data;
      //   } else {
      //     this.$toast(res.msg);
      //   }
      // });
      // getPage({
      //   id: "1295522665976070144",
      // }).then(res => {
      //   loading.clear();
      //   if (res.code == 0) {
      //     this.agreement = res.data.scCmsText.tmsg.replace(
      //       /(<img[\s\S]+?)src=(['"])/g,
      //       '<img src="' + this.$config.http
      //     );
      //   } else {
      //     this.$toast(res.msg);
      //   }
      // });
      if (this.$route.query.id) {
        getGoodsDetail({
          gcode: this.$route.query.id,
          shopUrl: "",
        }).then(res => {
          loading.clear();
          if (res.code == 0) {
            this.goodsDetail = res.data;
            this.pics = res.data.gimg.split(",");
            this.msg = res.data.gmsg.replace(
              /(<img[\s\S]+?)src=(['"])/g,
              '<img src="' + this.$config.http
            );
          } else {
            this.$toast(res.msg);
          }
        });
      }
    },
    share() {
      let that = this;
      wxApi()
        .then(res => {
          this.$wx.config({
            debug: false,
            appId: res.data.appId,
            timestamp: res.data.timestamp,
            nonceStr: res.data.nonceStr,
            signature: res.data.signature,
            jsApiList: ["onMenuShareTimeline", "onMenuShareAppMessage"],
          });
          this.$wx.ready(function() {
            that.$wx.onMenuShareAppMessage({
              title: that.goodsDetail.gname, // 分享标题
              desc: that.goodsDetail.gname, // 分享描述
              link:
                that.$config.http +
                "/wx/#/cardThree?id=" +
                that.$route.query.id +
                "&parentId=" +
                sessionStorage.getItem("userId") +
                "&title=" +
                that.$route.query.title, // 分享链接
              imgUrl: that.$utils.dealImg(that.goodsDetail.pics[0]), // 分享图标
              type: "link", // 分享类型,music、video或link，不填默认为link
              dataUrl: "", // 如果type是music或video，则要提供数据链接，默认为空
              success: function() {
                // 用户确认分享后执行的回调函数
                that.$toast("分享成功");
              },
              cancel: function() {
                // 用户取消分享后执行的回调函数
                that.$toast.fail("取消分享");
              },
            });
            that.$wx.onMenuShareTimeline({
              title: that.goodsDetail.gname, // 分享标题
              link:
                that.$config.http +
                "/wx/#/cardThree?id=" +
                that.$route.query.id +
                "&parentId=" +
                sessionStorage.getItem("userId") +
                "&title=" +
                that.$route.query.title, // 分享链接
              imgUrl: that.$utils.dealImg(that.goodsDetail.pics[0]), // 分享图标
              success: function() {
                that.$toast("分享成功");
              },
              cancel: function() {
                // 用户取消分享后执行的回调函数
                that.$toast.fail("取消分享");
              },
            });
          });
        })
        .catch(err => {
          console.log(err);
        });
    },
  },
  mounted() {
    document.title = this.$route.query.title;
    this.loadData();
    this.share();
  },
};
</script>
<style lang="scss" scoped>
.one {
  background: #fff;
  .top {
    img {
      width: 100%;
      display: block;
    }
    &_mid {
      padding: 0 20px;
      background: #4981f5;
    }
    &_bottom {
      background: #4981f5;
      padding: 15px 20px;
      color: #fff;
      display: flex;
      align-items: center;
      .left {
        margin: 0 10px;
      }
      .flex-mid {
        flex: 2;
        .notice-swipe {
          width: 100%;
          height: 20px;
          line-height: 20px;
          text-align: center;
        }
      }
      .right {
        margin: 0 10px;
        width: 80px;
        line-height: 25px;
      }
    }
  }

  .fixed {
    position: fixed;
    bottom: 20px;
    width: calc(100% - 80px);
    margin: 0 40px;
    .flex {
      display: flex;
      align-items: center;
      justify-content: center;
      background: #f27900;
      color: #fff;
      line-height: 45px;
      border-radius: 5px;
      .left {
        border-right: 2px solid #fff;
        line-height: 18px;
        padding: 0 10px 0 0;
      }
      .right {
        margin: 0 0 0 10px;
      }
    }
  }
}
</style>
<style lang="scss">
.one {
  .bottom {
    .content {
      img {
        width: 100%;
        display: block;
      }
    }
  }
}
</style>
